<script>
  import { DeckEncoder } from "runeterra";
  import qs from 'qs'
  import CardBar from "./CardBar.svelte";

  let deckcode = "CEBQCAQDAQCQCBABCETTINQHAEBQEDAPCQPCKKAAAEAQEAYD";
  let url = window.location.href;
  let param = qs.parse(url.split('?')[1]);
  if (param.code) deckcode = param.code;
  let deck;
  decode();
  function decode() {
    deck = DeckEncoder.decode(deckcode);
  }
</script>

<style>

  deckcode{
    width:90%;
  margin: 1em auto;
	border: 1px solid rgb(73, 52, 31);
	font: 2em;
    display:flex;
    vertical-align: middle;
  }
  label{
    background: rgb(73, 52, 31);
    color: white;
    text-align: center;
  line-height: 200%;
    font-weight: bold;
    padding: 0 1em;
  }
  main {
    margin: 0 auto;
    width:22em; 
  }

  input {
    margin:0;
    flex-grow: 3;
    padding: .5em;
    background: rgb(199, 184, 169);
    	color: #321;


}
</style>
<deckcode>
<label>牌組代碼</label><input on:change={decode} bind:value={deckcode} placeholder="將代碼貼在此" />
</deckcode>

<main>
  {#each deck as card}
    <CardBar {card} />
  {/each}
</main>
